                    >
                      <q-icon name="icon-save-01" size="xs" />
                      <span class="q-pl-xs">Save Changes</span>
                    </q-btn>
                  </div>
                </div>
              </q-card-section>
              <q-card-section
                id="description"
                class="q-px-none hover-visibility"
              >
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick ? void 0 : clickScrollEvent('description')
                  "
                >
                  Description
                </div>
                <div v-if="!editing">
                  <div class="raw-html" v-html="overviewData.description"></div>
                </div>
                <div v-else>
                  <q-editor
                    ref="descriptionEditorRef"
                    @paste="
                      (e) => (e) =>
                        pastePlainText(e, $refs['descriptionEditorRef'])
                    "
                    v-model="overviewData.description"
                    placeholder="Enter a description..."
                    height="250px"
                    :toolbar="[
                      [
                        {
                          icon: $q.iconSet.editor.align,
                          fixedLabel: true,
                          list: 'only-icons',
                          options: ['left', 'center', 'right', 'justify'],
                        },
                      ],
                      ['bold', 'italic', 'underline', 'link'],
                      [
                        {
                          label: $q.lang.editor.formatting,
                          icon: $q.iconSet.editor.formatting,
                          list: 'no-icons',
                          options: ['p', 'h6', 'code'],
                        },
                      ],
                    ]"
                  />
                </div>
              </q-card-section>
              <q-card-section id="target_audience" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick
                        ? void 0
                        : clickScrollEvent('target_audience')
                  "
                >
                  Target Audience
                </div>
                <div v-if="!editing">
                  <template
                    v-for="sentence in overviewData.target_audience.split('\n')"
                    :key="sentence"
                  >
                    <p>
                      {{ sentence }}
                    </p>
                  </template>
                </div>
                <div v-else>
                  <q-input
                    dense
                    outlined
                    type="textarea"
                    class="rounded"
                    v-model="overviewData.target_audience"
                  />
                </div>
              </q-card-section>

              <q-card-section id="video_walkthrough" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick
                        ? void 0
                        : clickScrollEvent('video_walkthrough')
                  "
                >
                  Video Walkthrough
                </div>

                <div v-if="!editing">
                  <q-video
                    :ratio="16 / 9"
                    class="rounded fit"
                    title="Video Walkthrough"
                    :src="overviewData.video_walkthrough"
                  />
                </div>
                <div v-else>
                  <q-input
                    dense
                    outlined
                    class="rounded"
                    v-model="overviewData.video_walkthrough"
                  />
                </div>
              </q-card-section>
              <q-card-section id="warning" class="q-px-none q-pb-md">
                <q-card flat class="rounded bg-color q-pa-md">
                  <div
                    class="target-clickable-id full-width"
                    @click="
                      () =>
                        noTitleClick ? void 0 : clickScrollEvent('warning')
                    "
                  >
                    <div
                      class="items-center flex text-negative font-medium q-gutter-x-sm"
                    >
                      <q-icon name="icon-alert-circle" class="font-medium" />
                      <span>Warning</span>
                    </div>
                  </div>
                  <div v-if="!editing" class="q-pt-sm text-primary-500">
                    {{ overviewData.warning }}
                  </div>
                  <div v-else class="q-pt-sm">
                    <q-input
                      dense
                      outlined
                      type="textarea"
                      class="rounded"
                      v-model="overviewData.warning"
                    />
                  </div>
                </q-card>
              </q-card-section>
              <q-card-section id="resources" class="q-px-none">
                <div
                  class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                  @click="
                    () =>
                      noTitleClick ? void 0 : clickScrollEvent('resources')
                  "
                >
                  Resources
                </div>
                <template v-if="!editing">
                  <div
                    id="resources-images"
                    class="row items-center justify-start q-col-gutter-md"
                  >
                    <div
                      v-for="i in 4"
                      :key="i"
                      :ratio="1"
                      class="rounded overflow-hidden col-12 col-sm-6"
                    >
                      <q-img
                        loading="lazy"
                        :src="`${$cdn_host}${$cdn.tmp}/Image-${i}.png`"
                        class="rounded fit with-lightbox"
                        spinner-color="secondary"
                        :alt="`Image-${i}`"
                      />
                    </div>
                  </div>
                  <div id="resources-content" class="q-pt-md">
                    {{ overviewData.resources_text }}
                    <ul>
                      <li
                        v-for="(link, i) in overviewData.resources_links"
                        :key="i"
                        class="text-secondary"
                      >
                        <external-link :url="link" color="text-secondary" />
                      </li>
                    </ul>
                  </div>
                </template>
                <div v-else>
                  <FilesUploader
                    multiple
                    ref="upResouces"
                    max-file-size="10000000"
                    note="AI, EPS, PSD, SVG, PNG, GIF or JPEG (max. 10MB)"
                    :filter="
                      (files) =>
                        checkFileExtensions(files, [
                          '.png',
                          '.ico',
                          '.jpg',
                          '.jpeg',
                          '.svg',
                          '.ai',
                          '.psd',
                          '.eps',
                        ])
                    "
                    @rejected="onRejected"
                    :removedFile="overviewData.resources_files?.removedFile"
                    @added="
                      (files) =>
                        addUploadToModel(
                          'overviewData',
                          'resources_files',
                          files,
                          'upResouces'
                        )
                    "
                  />
                  <div
                    v-if="overviewData.resources_files?.files?.length"
                    class="full-width scroll q-mt-md"
                    style="max-height: 175px"
                  >
                    <UploadSlot
                      :small="$q.screen.lt.md"
                      thumbnail="icon-image-03"
                      :scope="overviewData.resources_files"
                    />
                    <div
                      v-if="overviewData.resources_files?.files?.length > 2"
                      class="absolute-bottom bg-gradient no-pointer-events sticky"
                      style="height: 50px"
                    ></div>
                  </div>
                  <div class="q-pt-md">
                    <q-input
                      dense
                      outlined
                      type="textarea"
                      class="rounded"
                      v-model="overviewData.resources_text"
                    />
                  </div>
                </div>
              </q-card-section>
            </q-card>
          </template>
        </q-splitter>
      </q-card>
    </q-card>
    <q-scroll-observer @scroll="scrollHandler" />
    <image-lightbox
      :visible="showLightBox"
      :imgs="lightboxImages"
      :index="lightboxIndex"
      @hide="showLightBox = false"
    />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useUser from 'stores/user';
import { copyToClipboard, debounce, format } from 'quasar';
import { avatarNameAbbr } from 'src/assets/scripts/functions';
const { capitalize } = format,
  getEl = (id) => {
    return document.getElementById(id);
  };
import FilesUploader from 'src/components/Helpers/FilesUploader.vue';
import UploadSlot from 'src/components/Helpers/Slots/UploadSlot.vue';
import {
  FileUploader_Mixin,
  Editor_Mixin,
  Export_Task_Project_Mixin,
} from 'src/mixins';

export default defineComponent({
  name: 'ProjectOverview',

  mixins: [FileUploader_Mixin, Editor_Mixin, Export_Task_Project_Mixin],

  components: { FilesUploader, UploadSlot },

  data() {
    return {
      title: 'Project Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tabSectionId: '',
      tabSectionIdScrolled: '',
      splitterModel: 175,
      editing: false,
      noTitleClick: false,
      documentReady: false,
      avatarNameAbbr,
      capitalize,
      allSections: [
        { label: 'Description', key: 'description' },
        { label: 'Target Audience', key: 'target_audience' },
        { label: 'Video Walkthrough', key: 'video_walkthrough' },
        { label: 'Warning', key: 'warning' },
        { label: 'Resources', key: 'resources' },
      ],
      overviewData: {
        description:
          '<div>Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla. Elit nisi in eleifend sed nisi. Pulvinar at orci, proin imperdiet commodo consectetur convallis risus.</div><div><br/></div><div>Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.</div><div><br/></div><div>Elit nisi in eleifend sed nisi. Pulvinar at orci, proin imperdiet commodo consectetur convallis risus. Sed condimentum enim dignissim adipiscing faucibus consequat, urna. Viverra purus et erat auctor aliquam. Risus, volutpat vulputate posuere purus sit congue convallis aliquet. Arcu id augue ut feugiat donec porttitor neque. Mauris, neque ultricies eu vestibulum, bibendum quam lorem id. Dolor lacus, eget nunc lectus in tellus, pharetra, porttitor.</div>',
        target_audience:
          'Ipsum sit mattis nulla quam nulla. Gravida id gravida ac enim mauris id.Diam elit, orci, tincidunt aenean tempus. Quis velit eget ut tortor tellus. Sed vel, congue felis elit erat nam nibh orci. Non pellentesque congue eget consectetur turpis. Sapien, dictum molestie sem tempor. Diam elit, orci, tincidunt aenean tempus. Quis velit eget ut tortor tellus. Sed vel, congue felis elit erat nam nibh orci.\nDolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.',
        video_walkthrough:
          'https://www.loom.com/embed/c2744e314e2b43f0bcad53c96dfb9741',
        warning:
          'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Quis praesentium cumque magnam odio iure quidem, quod illum numquam possimus obcaecati commodi minima assumenda consectetur culpa fuga nulla ullam. In, libero.',
        resources_text:
          'Dolor enim eu tortor urna sed duis nulla. Aliquam vestibulum, nulla odio nisl vitae. In aliquet pellentesque aenean hac vestibulum turpis mi bibendum diam. Tempor integer aliquam in vitae malesuada fringilla.',
        resources_links: ['drive.google.com', 'loom.com'],
        resources_files: null,
      },
      scrollHandler: debounce(this.scrollHandlerOrig, 100),
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands', 'selectedOrgProjects']),

    hashId() {
      return this.$route.hash.slice(1);
    },

    brandId() {
      return this.$route.params?.brandId;
    },

    projectId() {
      return this.$route.params?.projectId;
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brandId] || {};
    },

    projectInfo() {
      return this.selectedOrgProjects[this.brandId]?.[this.projectId] || {};
    },
  },

  watch: {
    editing: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
      },
    },
    tabSectionId: {
      async handler(val) {
        if (val) {
          // this.$router.push({ hash: '#' + val });
          this.scrollToTarget(val);
          this.tabSectionId = '';
          // this.tabSectionIdScrolled = '';
        }
      },
    },
  },

  mounted() {
    this.$root.setMeta({ title: this.title });
    this.tabSectionId = this.hashId;
    this.updateLightBox();
    this.$nextTick(() => {
      setTimeout(() => {
        this.documentReady = true;
      }, 1000);
    });
  },

  methods: {
    updateLightBox() {
      this.lightboxImages = [];
      const lightboxElems = document.querySelectorAll('.with-lightbox');
      lightboxElems.forEach((e, i) => {
        let img = e.querySelector('img');
        this.lightboxImages.push({
          src: img.src,
          title: img.title,
          alt: img.alt,
        });
        e.onclick = () => {
          this.lightboxIndex = i;
          this.showLightBox = true;
        };
      });
    },

    scrollToTarget(targetId) {
      getEl(targetId)?.scrollIntoView?.({
        block: 'start',
        inline: 'nearest',
        behavior: 'smooth',
      });
    },

    clickScrollEvent(targetId, copy = true) {
      this.tabSectionId = targetId;
      // Copy URL to Clipboard
      if (copy) {
        copyToClipboard(
          window.location.origin + this.$route.fullPath + '#' + targetId
        );
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Anchor has been copied to clipboard.',
          timeout: 500,
        });
      }
    },

    scrollHandlerOrig(scroll) {
      this.tabSectionIdScrolled = '';
      let selectedKey = '';
      this.allSections
        .map((e) => e.key)
        .reverse()
        .forEach((key) => {
          const el = getEl(key),
            offsetTop = el?.offsetTop + 25,
            top = scroll?.position?.top;
          if (top > offsetTop && !selectedKey) {
            selectedKey = key;
          }
        });
      this.tabSectionIdScrolled = selectedKey;
    },
  },
});
</script>
